/*  */
import React, { useState, useEffect } from "react";
import axios from "axios";

/*  */
import LeftPlusBtn from './LeftPlusBtn'
import LeftContents from './LeftContents'
import NotionModal from './NotionModal.js'
import Cards from './Cards.js'
import DragAndDrop from '../DragAndDrop/DragAndDrop.js'




function LeftSidebar() {
  /*  ,  */
  const [notionmodal, setnotionmodal] = useState(false);

  /*  input send */
  const [data, setdata] = useState('')//input
  /* response  */
  const [res, setres] = useState([])

  /* data back    */
  const forSendData = {
    role: "213",// 213 
    content: data,
  }
  
  /* input(data) back , response  res   */
  async function sendData() {
    try {

      /* back data  */
      const dataResponse = await axios.post("/api/chat", forSendData);

      /* res   */
      const dataCopy = [...res];
      dataCopy.push(dataResponse);
      setres(dataCopy);

    } catch (error) {
      console.log(error);
    }
  }


  return (
    <div className="sideBar">

      {/*   */}
      <LeftPlusBtn onChange={function () {
        setnotionmodal(!notionmodal);}}/>

      {/*    */}
      <LeftContents setdata={setdata} sendData={sendData}/>

      <DragAndDrop/>

      {/*  response     */}
      {
        res.map(function (a) {
          return (
            <Cards a={a}/>
          )
        })
      }

      {/*  */}
      {notionmodal === true ? <NotionModal /> : null}
    </div>
  );
}


export default LeftSidebar;